.card {
  margin: 10px 0;
}

.card:hover {
  box-shadow: 5px 10px 20px 1px rgba(175, 175, 175, 0.25) !important;
  transition: ease-in 250ms;
}

.card-body {
  padding: 3rem 0 !important;
}

.card-text {
  font-size: 0.9rem;
  padding: 0.4rem 1.9rem;
  text-align: left;
  font-weight: bold;
}

.container-fluid .row {
  padding-top: 6rem;
  margin: 0 auto;
}

.card-body .btn {
  margin: 10px;
}

.container-fluid {
  background-color: #202020;
  min-height: 100vh;
  width: 100%;
}

.techimg {
  margin: 15px;
}

#root > div > div.container-fluid > div > div > div {
  border-radius: 5px;
  border: none;
}
