.home-body {
  margin: 0;
}

.contacts {
  display: flex;
  position: absolute;
  flex-direction: column;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.contacts h1 {
  font-weight: 700;
  font-size: 30px;
  letter-spacing: 9px;
  text-transform: uppercase;
  margin: 20px 0;
  color: #fff;
  text-align: center;
}

.contacts h2 {
  color: #999;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.12em;
  margin-bottom: 20px;
  text-align: center;
}

.contacts p {
  color: #999;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.12em;
  margin-bottom: 20px;
  text-align: center;
}

.social {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 20px;
}

.social i {
  font-size: 2.8rem;
  color: #999;
}

.social i:hover {
  color: #fff;
  transition: ease-in-out 250ms;
}
